import {Figure} from "./Figure";
import {GeometryPi, IPoint} from "../GeometryPi";
import {G} from "@svgdotjs/svg.js";
import {Point} from "./Point";
import {Arc} from "./Arc";
import {PointUser} from "./PointUser";
import {Label} from "../Label";
import {Line} from "./Line";
import _math from "../helpers";

export interface ITriangle {
    [key: string]: number
}

export class Triangle extends Figure {
    private _A: Point;
    private _B: Point;
    private _C: Point;
    private _refA: Point;
    private _refB: Point;
    private _scale: number;
    private _calculated: ITriangle;
    private _a: number;
    private _b: number;
    private _c: number;
    private _alpha: number;
    private _beta: number;
    private _gamma: number;
    private _solution: number

    private _rectangleMark: Boolean;
    private _rectangleMarkOnly: Boolean;
    private _arcBAC: Arc;
    private _arcCBA: Arc;
    private _arcACB: Arc;
    private _AB: Line;
    private _BC: Line;
    private _CA: Line;
    private _internalFigures: (Point|Line|Arc)[];

    constructor(geom: GeometryPi, name: string) {
        super(geom, name);

        // The shape.
        this.shape = geom.draw.polygon([0, 0, 0, 0]).stroke('black').fill('none');

        // Define the default points.
        this._A = new PointUser(geom, `${name}-point-A`, 0, 0);
        this._B = new PointUser(geom, `${name}-point-B`, 0, 0);
        this._C = new PointUser(geom, `${name}-point-C`, 0, 0);

        // Define the segment
        this._AB = new Line(geom, `${name}-segment-AB`, this._A, this._B).segment();
        this._BC = new Line(geom, `${name}-segment-BC`, this._B, this._C).segment();
        this._CA = new Line(geom, `${name}-segment-CA`, this._C, this._A).segment();

        // Define the arcs
        this._rectangleMark = true;
        this._rectangleMarkOnly = false;
        this._arcBAC = null;
        this._arcCBA = null;
        this._arcACB = null;
        this._createAngleMark();

        // The arcs are special cases
        this._internalFigures = [
            this._A, this._B, this._C,
            this._AB, this._BC, this._CA,
            this._arcBAC, this._arcACB, this._arcCBA
        ]
        this.hideAll();
        this.showAnglesMarks();

        // Reference points and values
        this._refA = null;
        this._refB = null;
        this._scale = null;

        // Init data.
        this._resetData();

        return this;
    }

    _resetData(): Triangle {
        this._a = null;
        this._b = null;
        this._c = null;
        this._alpha = null;
        this._beta = null;
        this._gamma = null;
        this._solution = null;

        return this;
    }

    base(A: Point, B?: Point): Triangle {
        if(this._solution===null){return this;}

        this._refA = A;
        if (B !== undefined) {
            this._refB = B;
        } else {
            this._refB = null;
        }

        this.update();
        return this;
    }

    define(data: any): Triangle {
        this._resetData();

        if (data.a !== undefined) {
            this._a = data.a;
        }
        if (data.b !== undefined) {
            this._b = data.b;
        }
        if (data.c !== undefined) {
            this._c = data.c;
        }
        if (data.alpha !== undefined) {
            this._alpha = data.alpha;
        }
        if (data.beta !== undefined) {
            this._beta = data.beta;
        }
        if (data.gamma !== undefined) {
            this._gamma = data.gamma;
        }
        if (data.solution !== undefined) {
            this.solution(data.solution, false);
        }

        this._calculated = this._calculateTriangle(this._a, this._b, this._c, this._alpha, this._beta, this._gamma, this._solution);
        this.update();
        return this;
    }

    solution(id: number, doUpdate?: boolean): Triangle {
        if (id === undefined || id < 1 || id > 2) {
            id = 1;
        }

        this._solution = +id;
        if (doUpdate) {
            this._calculated = this._calculateTriangle(this._a, this._b, this._c, this._alpha, this._beta, this._gamma, this._solution);
            this.update();
        }
        return this;
    }

    /* All this part should be moved elsewhere */
    private _numberOfKnownAngles(alpha: number, beta: number, gamma: number) {
        return [alpha, beta, gamma].filter(value => value !== null).length;
    }

    private _numberOfKnownSides(a: number, b: number, c: number) {
        return [a, b, c].filter(value => value !== null).length;
    }

    private _sidesAreAdjacent(a: number, b: number, c: number, alpha: number, beta: number, gamma: number): boolean {
        // This one is called if there are two sides.
        if (a === null) {
            return alpha !== null;
        }
        if (b === null) {
            return beta !== null;
        }
        if (c === null) {
            return gamma !== null;
        }

        return false;
    }

    private _calculateTriangle3Sides(a: number, b: number, c: number): ITriangle {
        let tri: ITriangle = {
            a: null,
            b: null,
            c: null,
            alpha: null,
            beta: null,
            gamma: null,
            solution: 1
        };

        tri.a = +a;
        tri.b = +b;
        tri.c = +c;
        tri.alpha = Math.acos((Math.pow(c, 2) + Math.pow(b, 2) - Math.pow(a, 2)) / (2 * c * b)) * 180 / Math.PI
        tri.beta = Math.acos((Math.pow(c, 2) + Math.pow(a, 2) - Math.pow(b, 2)) / (2 * c * a)) * 180 / Math.PI
        tri.gamma = Math.acos((Math.pow(a, 2) + Math.pow(b, 2) - Math.pow(c, 2)) / (2 * a * b)) * 180 / Math.PI
        return tri;
    }

    private _calculateTriangleOneSideTwoAngles(a: number, b: number, c: number, alpha: number, beta: number, gamma: number): ITriangle {
        let tri: ITriangle = {
            a: null,
            b: null,
            c: null,
            alpha: null,
            beta: null,
            gamma: null,
            solution: 1
        };

        // One side and two angles.
        tri.alpha = alpha === null ? 180 - beta - gamma : +alpha;
        tri.beta = beta === null ? 180 - alpha - gamma : +beta;
        tri.gamma = gamma === null ? 180 - beta - alpha : +gamma;

        if (a !== null) {
            tri.a = +a;
            tri.b = a * Math.sin(tri.beta * Math.PI / 180) / Math.sin(tri.alpha * Math.PI / 180);
            tri.c = a * Math.sin(tri.gamma * Math.PI / 180) / Math.sin(tri.alpha * Math.PI / 180);
        } else if (b !== null) {
            tri.a = b * Math.sin(tri.alpha * Math.PI / 180) / Math.sin(tri.beta * Math.PI / 180);
            tri.b = +b;
            tri.c = b * Math.sin(tri.gamma * Math.PI / 180) / Math.sin(tri.beta * Math.PI / 180);
        } else if (c !== null) {
            tri.a = c * Math.sin(tri.alpha * Math.PI / 180) / Math.sin(tri.gamma * Math.PI / 180);
            tri.b = c * Math.sin(tri.beta * Math.PI / 180) / Math.sin(tri.gamma * Math.PI / 180);
            tri.c = +c;
        }

        return tri;
    }

    private _calculateThmSine(a: number, alpha: number, b: number, solution: number): number {
        if (solution === 1) {
            return Math.asin(Math.sin(alpha * Math.PI / 180) / a * b) * 180 / Math.PI;
        } else {
            return 180 - Math.asin(Math.sin(alpha * Math.PI / 180) / a * b) * 180 / Math.PI;
        }
    }

    private _calculateTriangle(a: number, b: number, c: number, alpha: number, beta: number, gamma: number, solution: number = 1): ITriangle {
        // First check if one angle is missing.
        let nbSides = this._numberOfKnownSides(a, b, c),
            nbAngles = this._numberOfKnownAngles(alpha, beta, gamma);

        // No sides given :(
        if (nbSides === 0) {
            return;
        }

        // Three sides
        if (nbSides === 3) {
            return this._calculateTriangle3Sides(a, b, c);
        }

        // One side and two angles
        if (nbSides === 1 && nbAngles === 2) {
            return this._calculateTriangleOneSideTwoAngles(a, b, c, alpha, beta, gamma);
        }

        // Two sides and one angle -> means two angles.
        if (nbSides === 2 && nbAngles === 1) {
            // Either we know the angle between the two known sides
            // Or not -> two solution
            if (this._sidesAreAdjacent(a, b, c, alpha, beta, gamma)) {
                if (alpha !== null) {
                    return this._calculateTriangle3Sides(Math.sqrt(Math.pow(b, 2) + Math.pow(c, 2) - 2 * b * c * Math.cos(alpha * Math.PI / 180)), b, c);
                } else if (beta !== null) {
                    return this._calculateTriangle3Sides(a, Math.sqrt(Math.pow(a, 2) + Math.pow(c, 2) - 2 * a * c * Math.cos(beta * Math.PI / 180)), c);
                } else if (gamma !== null) {
                    return this._calculateTriangle3Sides(a, b, Math.sqrt(Math.pow(b, 2) + Math.pow(c, 2) - 2 * b * c * Math.cos(gamma * Math.PI / 180)));
                }
            } else {
                // There are two solutions!
                if (alpha !== null) {
                    // alpha, a and ... b or c
                    if (b !== null) {
                        return this._calculateTriangleOneSideTwoAngles(a, null, null, alpha, this._calculateThmSine(a, alpha, b, solution), null);
                    } else if (c !== null) {
                        return this._calculateTriangleOneSideTwoAngles(a, null, null, alpha, beta, this._calculateThmSine(a, alpha, c, solution));
                    }
                } else if (beta !== null) {
                    if (a !== null) {
                        return this._calculateTriangleOneSideTwoAngles(null, b, null, this._calculateThmSine(b, beta, a, solution), beta, null);
                    } else if (c !== null) {
                        return this._calculateTriangleOneSideTwoAngles(null, b, null, null, beta, this._calculateThmSine(b, beta, c, solution));
                    }
                } else if (gamma !== null) {
                    if (a !== null) {
                        return this._calculateTriangleOneSideTwoAngles(null, null, c, this._calculateThmSine(c, gamma, a, solution), null, gamma);
                    } else if (b !== null) {
                        return this._calculateTriangleOneSideTwoAngles(null, null, c, null, this._calculateThmSine(c, gamma, b, solution), gamma);
                    }
                }
            }
        }

        // There was a problem calculating the triangle !
        return {
            a: null,
            b: null,
            c: null,
            alpha: null,
            beta: null,
            gamma: null,
            solution: 0
        };
    }

    /* UPDATING THE FIGURE */
    update(): Triangle {
        // Calculate the triangle using the given data.
        if (this._calculated.solution === 0) {
            return this;
        }

        let scale, csize, angleBeta;

        // Determine the shape scale if necessary.
        if (this._refB === null) {
            if (this._scale === null) {
                // Auto fit
                if (this._calculated.beta < 90) {
                    scale = Math.max(
                        (this.geometryPi.width - this._A.x - 50) / this._calculated.c,
                        (this.geometryPi.height - this._A.y - 50) / (this._calculated.b * Math.asin(this._calculated.alpha * Math.PI / 180))
                    )
                } else {
                    scale = Math.max(
                        (this.geometryPi.width - this._A.x - 50) / (this._calculated.b * Math.acos(this._calculated.alpha * Math.PI / 180)),
                        (this.geometryPi.height - this._A.y - 50) / (this._calculated.b * Math.asin(this._calculated.alpha * Math.PI / 180))
                    )
                }
            } else {
                scale = +this._scale;
            }
        }

        // Determine the various points
        // Point A
        if (this._refA === null) {
            // We must have a reference !!!!!
            // TODO: Possibility to auto fit the shape.
            this._A.x = 0;
            this._A.y = 0;
        } else {
            this._A.x = +this._refA.x;
            this._A.y = +this._refA.y;
        }

        // Point B
        if (this._refB === null) {
            this._B.x = +this._A.x + this._calculated.c * scale;
            this._B.y = +this._A.y;

            // Length of the c side
            csize = +this._calculated.c;

            // Angle between horizontal line and AB.
            angleBeta = 0;
        } else {
            this._B.x = this._refB.x;
            this._B.y = this._refB.y;

            // Length of the c side.
            csize = Math.sqrt(Math.pow(this._B.y - this._A.y, 2) + Math.pow(this._B.x - this._A.x, 2));

            // Angle between horizontal line and AB.
            angleBeta = Math.acos((this._B.x - this._A.x) / csize) * 180 / Math.PI;
            if (this._B.y - this._A.y > 0) {
                angleBeta = 360 - angleBeta;
            }

            // The scale to use for the C point
            scale = csize / this._calculated.c;
        }


        // Point C.
        this._C.x = this._A.x + this._calculated.b * scale * Math.cos((this._calculated.alpha + angleBeta) * Math.PI / 180);
        this._C.y = this._A.y - this._calculated.b * scale * Math.sin((this._calculated.alpha + angleBeta) * Math.PI / 180);


        // Update the shape.
        this.shape.plot([
                this._A.x, this._A.y,
                this._B.x, this._B.y,
                this._C.x, this._C.y
            ]
        );

        this._updateInternalPoints();

        // Update the shape.
        return this;
    }

    private _updateInternalPoints(){
        this._A.update();
        this._B.update();
        this._C.update();

        this._AB.update();
        this._BC.update();
        this._CA.update();

        this._updateAngleMark();
    }
    private _createAngleMark(){
        if(this._arcBAC===null){

            this._arcBAC = new Arc(this.geometryPi, `${this.name}-angle-BAC`)
                .center(this._A).radius(40).angleABC([this._B, this._A, this._C])
                .square(this._rectangleMark);
        }
        if(this._arcACB===null){

            this._arcACB = new Arc(this.geometryPi, `${this.name}-angle-ACB`)
                .center(this._C).radius(40).angleABC([this._A, this._C, this._B])
                .square(this._rectangleMark);
        }
        if(this._arcCBA===null){

            this._arcCBA = new Arc(this.geometryPi, `${this.name}-angle-CBA`)
                .center(this._B).radius(40).angleABC([this._C, this._B, this._A])
                .square(this._rectangleMark);
        }
    }
    private _updateAngleMark(){
        // Update teh elements.
        this._arcBAC.square(this._rectangleMark).update();
        this._arcCBA.square(this._rectangleMark).update();
        this._arcACB.square(this._rectangleMark).update();

        if(this._rectangleMarkOnly){
            if(this._arcBAC.isSquare){this._arcBAC.show();}else{this._arcBAC.hide();}
            if(this._arcACB.isSquare){this._arcACB.show();}else{this._arcACB.hide();}
            if(this._arcCBA.isSquare){this._arcCBA.show();}else{this._arcCBA.hide();}
        }

    }
    rectangleMark(value:Boolean):Triangle {
        this._rectangleMark = value!==false;
        this._updateAngleMark()
        return this;
    }
    rectangleMarkOnly(value:Boolean):Triangle {
        this._rectangleMarkOnly = value!==false;
        this._updateAngleMark()
        return this;
    }
    log(): Triangle {
        console.log('Donnée:')
        console.log({
            a: this._a,
            b: this._b,
            c: this._c,
            alpha: this._alpha,
            beta: this._beta,
            gamma: this._gamma
        });

        console.log('Calculés:')
        console.log(this._calculated);
        return this;
    }

    /* Access to the points, segments and arcs. */
    hideAll():Triangle{
        for(let f of this._internalFigures){
            f.hide()
        }
        return this;
    }
    hideAllLabel(exceptPoints?:Boolean):Triangle{
        for(let f of this._internalFigures){
            f.hideLabel();
        }
        return this;
    }
    showAnglesMarks():Triangle {
        this._arcBAC.show();
        this._arcCBA.show();
        this._arcACB.show();
        return this;
    }

    A(): Point {
        return this._A;
    }
    B(): Point {
        return this._B;
    }
    C(): Point {
        return this._C;
    }
    BAC(): Arc {
        return this._arcBAC;
    }
    ACB(): Arc {
        return this._arcACB;
    }
    CBA(): Arc {
        return this._arcCBA;
    }
    AB(): Line {
        return this._AB;
    }
    BC(): Line {
        return this._BC;
    }
    CA(): Line {
        return this._CA;
    }

    /* Label auto naming values */
    showLabelData(onlyUserData?:Boolean): Triangle {
        if(onlyUserData===undefined){onlyUserData = true;}

        if(onlyUserData){
            this.hideAllLabel();
        }

        if(this._a!==null){
            this._BC.labelText(this._a.toString()).showLabel();
        }
        if(this._b!==null){
            this._CA.labelText(this._b.toString()).showLabel();
        }
        if(this._c!==null){
            this._AB.labelText(this._c.toString()).showLabel();
        }

        if(this._alpha!==null && !this._arcBAC.isSquare){
            this._arcBAC.labelText(this._alpha.toString()).showLabel();
        }
        if(this._beta!==null && !this._arcCBA.isSquare){
            this._arcCBA.labelText(this._beta.toString()).showLabel();
        }
        if(this._gamma!==null && !this._arcACB.isSquare){
            this._arcACB.labelText(this._gamma.toString()).showLabel();
        }
        return this;
    }
    showCalculated(fix:number=1, fixAngle?:number):Triangle {

        if(fixAngle===undefined){fixAngle = fix;}

        if(this._a===null){
            this._BC.labelText(_math.round(this._calculated.a, fix).toFixed(fix)).showLabel();
        }
        if(this._b===null){
            this._CA.labelText(_math.round(this._calculated.b, fix).toFixed(fix)).showLabel();
        }
        if(this._c===null){
            this._AB.labelText(_math.round(this._calculated.c, fix).toFixed(fix)).showLabel();
        }

        if(this._alpha===null && !this._arcBAC.isSquare){
            this._arcBAC.labelText(_math.round(this._calculated.alpha, fixAngle).toFixed(fixAngle)).showLabel();
        }
        if(this._beta===null && !this._arcCBA.isSquare){
            this._arcCBA.labelText(_math.round(this._calculated.beta, fixAngle).toFixed(fixAngle)).showLabel();
        }
        if(this._gamma===null && !this._arcACB.isSquare){
            this._arcACB.labelText(_math.round(this._calculated.gamma, fixAngle).toFixed(fixAngle)).showLabel();
        }
        return this;
    }



    /* Custom getter */
    getDimensions(): ITriangle {
        return this._calculateTriangle(this._a, this._b, this._c, this._alpha, this._beta, this._gamma, this._solution);
    }

    coordinateA(A?: Point, angleBase?: number): IPoint {
        return {
            x: this._A.x,
            y: this._A.y
        }
    }

    coordinateB(A?: Point, angleBase?: number): IPoint {
        return {
            x: this._B.x,
            y: this._B.y
        }
    }

    coordinateC(A?: Point, angleBase?: number): IPoint {
        return {
            x: this._C.x,
            y: this._C.y
        }
    }
}