import {IPoint} from "./GeometryPi";
import {Point} from "./figures/Point";
import {PointCalculated} from "./figures/PointCalculated";

export class Vector {
    private _x:number;
    private _y: number;
    constructor(x?:number, y?:number) {
        if(x!==undefined && y!==undefined) {
            this._x = x;
            this._y = y;
        }else{
            this._x = 0;
            this._y = 0;
        }
        return this;
    }

    from(A: Point, B:Point):Vector{
        this._x = B.x-A.x;
        this._y = B.y-A.y;
        return this;
    }

    clone():Vector{
        return new Vector(this._x, this._y);
    }
    perpendicular():Vector{
        let x = +this._x,y = +this._y;

        // Predictive way of building perpendicular
        // Counterclockwise !
        this._x = y;
        this._y = -x;

        return this;
    }

    norme():number{
        return Math.sqrt(
            Math.pow(this._x, 2) +
            Math.pow(this._y, 2)
        );
    }

    unit():Vector{
        let n = this.norme();
        if(n===0){return this;}

        return this.multiply(1/n);
    }

    isNull():Boolean{
        return this.norme()===0;
    }

    add(v:Vector):Vector{
        this._x += v.x;
        this._y += v.y;
        return this;
    }

    sub(v:Vector):Vector{
        return this.add(v.clone().opposed());
    }

    multiply(k: number):Vector{
        this._x *= k;
        this._y *= k;
        return this;
    }

    opposed():Vector{
        this._x = -this._x;
        this._y = -this._y;
        return this;
    }

    static distance(p1:IPoint, p2:IPoint):number {
        return Math.sqrt(
            Math.pow(p2.x-p1.x, 2) +
            Math.pow(p2.y-p1.y, 2)
        );
    }

    static midPoint(p1: IPoint, p2: IPoint):IPoint {
        return {
            x: (p1.x+p2.x)/2,
            y: (p1.y+p2.y)/2
        };
    }

    get x(): number {
        return this._x;
    }

    set x(value: number) {
        this._x = value;
    }

    get y(): number {
        return this._y;
    }

    set y(value: number) {
        this._y = value;
    }
}