import {Text} from "@svgdotjs/svg.js";
import {GeometryPi} from "./GeometryPi";
import {Point} from "./figures/Point";
import {Figure} from "./figures/Figure";

export class Label{
    private _geometryPi: GeometryPi;
    private _refFigure: Figure;
    private _text: string;
    private _textPoint: Point;
    private _textDistance: Point[];
    private _textFunction: Function;
    private _textFunctionArgs: {};
    private _textRounding: number;
    private _textScale:number

    private _position: {horizontal: string, vertical: string}
    private _offset: {dx: number, dy: number}
    private _distance: number;
    private _shape: Text;
    private _x: number;
    private _y: number;

    constructor(gPi:GeometryPi, text:string, refFigure?:Figure) {
        this._geometryPi = gPi

        if(refFigure!==undefined){
            this._refFigure = refFigure;
        }
        this._text = text;
        this._textPoint = null;
        this._textDistance = null;
        this._textFunction = null;
        this._textFunctionArgs = {};

        this._position = {
            horizontal: 'left',
            vertical: 'bottom'
        };
        this._offset = {
            dx: 0,
            dy: 0
        };
        this._distance = 15;

        this._shape = this._geometryPi.draw.text('');
        this._shape.leading(0.0);

        // Set defaults
        this._shape.font({
            size: this._geometryPi.defaults.font.size
        });
        if(this._geometryPi.defaults.font.family!==null){
            this._shape.font({family: this._geometryPi.defaults.font.family})
        }

        this.updateText();
        return this;
    }

    update(): Label{
        this.move(this._x, this._y);
        return this;
    }
    updateText(){
        let text = this._textFunction===null?this._text:this._textFunction(this, this._textFunctionArgs);

        // Formating text
        let texts = [], txt = '', defaultY = 5, dy = 0,
        indices = false, symbol = false;
        for(let t of text){
            if(t==='_'){
                texts.push({text: txt, dy: +dy, indices: indices===true, symbol: symbol===true})
                indices = true;
                txt = '';
                dy = +defaultY;
                continue;
            }

            if(t==='\\'){
                texts.push({text: txt, dy: +dy, indices: indices===true, symbol: symbol===true})
                symbol = true;
                txt = '';
                continue;
            }

            if(t===' '){
                texts.push({text: txt, dy: +dy, indices: indices===true, symbol: symbol===true})
                if(indices) {
                    dy = -defaultY;
                }else{
                    dy = 0;
                }
                symbol = false;
                indices = false;
                txt = '';
                continue;
            }

            txt += t;
        }
        texts.push(
            {text: txt, dy: +dy, indices: indices===true, symbol: symbol===true}
        )

        this._shape.text('');
        this._shape.build(true);
        for(let t of texts) {
            let tspan = this._shape.tspan(t.symbol?this.greekLetter(t.text):t.text)
                .dy(t.dy)
                .font('font-size', t.indices?'110%':'150%');
            if(t.symbol){tspan.addClass('greek');}
        }
        this._shape.build(false);

        /**
         if(this._textFunction===null) {
            if (this._text.match(/\d+/g) === null) {
                this._shape.text(this._text);
            } else {
                // There are indices - make them as indices!
                let text = this._text,
                    dy = 5,
                    posIndices = false;
                this._shape.text(function (add) {
                    for (let t of text) {
                        if (isNaN(Number(t))) {
                            add.tspan(t).dy(posIndices ? -dy : 0);
                            posIndices = false;
                        } else {
                            add.tspan(t).dy(posIndices ? 0 : dy).font('size', '150%').font('anchor', 'middle')
                            posIndices = true;
                        }
                    }
                });
            }

            this.appendCoordinates();
            this.appendDistance();
        }else{
            this._shape.text(this._textFunction(this, this._textFunctionArgs))
        }
         */
    }

    greekLetter(letter:string):string{
        switch (letter) {
            case 'a': return 'α';
            case 'b': return 'β' ;
            case 'c': return 'γ';
            case 'd': return 'δ';
            case 'e': return 'ε';
            case 'z': return 'ζ';
            case 'j': return 'η';
            case 'h': return 'θ';
            case 'i': return 'ι';
            case 'k': return 'κ';
            case 'l': return 'λ';
            case 'm': return 'μ';
            case 'n': return 'ν';
            case 'x': return 'ξ';
            case 'o': return 'ο';
            case 'p': return 'π';
            case 'r': return 'ρ';
            case 's': return 'σ';
            case 't': return 'τ';
            case 'u': return 'υ';
            case 'f': return 'φ';
            case 'q': return 'χ';
            case 'y': return 'ψ';
            case 'w': return 'ω';
        }

        /*switch (letter) {
            case 'a': return '&#945;';
            case 'b': return '&#946;';
            case 'c': return '&#947;';
            case 'd': return '&#948;';
            case 'e': return '&#949;';
            case 'z': return '&#950;';
            case 'j': return '&#951;';
            case 'h': return '&#952;';
            case 'i': return '&#953;';
            case 'k': return '&#954;';
            case 'l': return '&#955;';
            case 'm': return '&#956;';
            case 'n': return '&#957;';
            case 'x': return '&#958;';
            case 'o': return '&#959;';
            case 'p': return '&#960;';
            case 'r': return '&#961;';
            case 's': return '&#963;';
            case 't': return '&#964;';
            case 'u': return '&#965;';
            case 'f': return '&#966;';
            case 'q': return '&#967;';
            case 'y': return '&#968;';
            case 'w': return '&#969;';
        }*/

        /*
        switch (letter) {
            case 'a': return '&alpha;';
            case 'b': return '&beta;';
            case 'c': return '&gamma;';
            case 'd': return '&delta;';
            case 'e': return '&epsilon;';
            case 'z': return '&zeta;';
            case 'j': return '&eta;';
            case 'h': return '&theta;';
            case 'i': return '&iota;';
            case 'k': return '&kappa;';
            case 'l': return '&lambda;';
            case 'm': return '&mu;';
            case 'n': return '&nu;';
            case 'x': return '&xi;';
            case 'o': return '&omicron;';
            case 'p': return '&pi;';
            case 'r': return '&rho;';
            case 's': return '&sigma;';
            case 't': return '&tau;';
            case 'u': return '&upsilon;';
            case 'f': return '&phi;';
            case 'q': return '&chi;';
            case 'y': return '&psi;';
            case 'w': return '&omega;';
        }
         */
        return letter;
    }
    appendCoordinates() {
        if(this._textPoint!==null){
            let coord = this._textPoint.coord;
            this._shape.build(true);
            this._shape.tspan(`(${(coord.x*this._textScale).toFixed(this._textRounding)};${(coord.y*this._textScale).toFixed(this._textRounding)})`);
            this._shape.build(false);
        }
    }

    appendDistance() {
        if(this._textDistance!==null){
            let p1 = this._textDistance[0].coord,
                p2 = this._textDistance[1].coord,
                d = Math.sqrt(
                    Math.pow(p2.x-p1.x,2)+
                    Math.pow(p2.y-p1.y,2)
                )
            this._shape.build(true);
            this._shape.tspan((d*this._textScale).toFixed(this._textRounding));
            this._shape.build(false);
        }
    }

    move(x:number, y:number):Label{
        this._x = x;
        this._y = y;

        // Update text if necessary before moving the label.
        if(this._textPoint!==null || this._textDistance!==null || this._textFunction!==null){
            this.updateText();
        }

        // Reference point.
        let posX = +x, posY = +y;

        // Add spaces between the reference point and the anchor.
        if(this._position.horizontal==='left'){
            posX = posX - this.w/2 - this._distance ;
        }else if(this._position.horizontal==='right'){
            posX = posX + this.w/2 + this._distance;
        }

        if(this._position.vertical==='top'){
            posY = posY - this.h/2 - this._distance;
        }else if(this._position.vertical==='bottom'){
            posY = posY + this.h/2 + this._distance;
        }

        // Modify the position.
        posX += this._offset.dx;
        posY += this._offset.dy;

        this.shape.cx(posX).cy(posY);

        return this;
    }

    show():Label {
        this.shape.show();
        return this;
    }
    hide(): Label {
        this.shape.hide();
        return this;
    }

    top():Label {
        this._position.vertical = 'top';
        this.move(this._x, this._y);
        return this;
    }
    bottom():Label {
        this._position.vertical = 'bottom';
        this.move(this._x, this._y);
        return this;
    }
    center():Label {
        this._position.vertical = 'center';
        this.move(this._x, this._y);
        return this;
    }
    left():Label {
        this._position.horizontal = 'left';
        this.move(this._x, this._y);
        return this;
    }
    right():Label {
        this._position.horizontal = 'right';
        this.move(this._x, this._y);
        return this;
    }
    middle():Label {
        this._position.horizontal = 'middle';
        this.move(this._x, this._y);
        return this;
    }
    resetPos():Label {
        this._offset.dx = 0;
        this._offset.dy = 0;
        this._position.horizontal = 'middle';
        this._position.vertical = 'center';
        this.move(this._x, this._y);
        return this;
    }
    distance(value: number):Label {
        this._distance = value;
        this.move(this._x, this._y);
        return this;
    }
    font(fontName: string):Label {
        this._shape.font('family', fontName);
        this.update();
        return this;
    }
    fontSize(fontSize: string):Label {
        this._shape.font('size', fontSize);
        this.update();
        return this;
    }

    offset(x:number, y:number):Label{
        this._offset.dx = +x;
        this._offset.dy = +y;
        this.update();
        return this;
    }
    polar(radius: number, angle: number):Label{
        this._offset.dx = Math.cos(angle*Math.PI/180)*radius;
        this._offset.dy = -Math.sin(angle*Math.PI/180)*radius;
        this.update();
        return this;
    }

    showCoordinates(p: Point, round: number = 0, scale: number = 1):Label{
        this._textPoint = p;
        this._textRounding = round;
        this._textScale = scale;
        // TODO: Use the textFunction system to display properly
        this.update();
        return this;
    }
    showDistance(p1:Point, p2: Point, round: number = 0, scale: number = 1):Label{
        this._textDistance = [p1, p2];
        this._textRounding = round;
        this._textScale = scale;
        this.update();
        return this;
    }
    showTextFunction(fn: Function, fnArgs: {}){
        this._textFunction = fn;
        this._textFunctionArgs = fnArgs;

        this.update();
        return this;
    }

    disablePointer(): Label {
        this.shape.node.style.pointerEvents="none";
        return this;
    }
    enablePointer(): Label {
        this.shape.node.style.pointerEvents="auto";
        return this;
    }

    get shape(): Text {
        return this._shape;
    }

    get w(): number {
        return this.shape.length();
    }

    get h(): number {
        return this.shape.bbox().h;
    }

    get text(): string {
        return this._text;
    }

    set text(value: string) {
        this._text = value;
    }

    get figure():Figure {
        return this._refFigure;
    }

    get labelDistance(): number {
        return this._distance
    }
}