import {GeometryPi} from "./GeometryPi";
import {Line} from "./figures/Line";
import {PointUser} from "./figures/PointUser";
import {Grid} from "./Grid";
import {Figure} from "./figures/Figure";
import {Point} from "./figures/Point";

export class Axis{
    private _geometryPi: GeometryPi;
    private _grid: Grid;
    private _xAxis: Figure;
    private _yAxis: Figure;
    private _origin: Point;

    constructor(gPi:GeometryPi) {
        this._geometryPi = gPi;
        this._grid = gPi.mainGrid;

        this._geometryPi.addPoint(this._grid.minX+0.5, 0,'OxMin').setVirtual();
        this._geometryPi.addPoint(this._grid.maxX-0.5, 0,'OxMax').setVirtual();
        this._geometryPi.addPoint(0, this._grid.minY+0.5,'OyMin').setVirtual();
        this._geometryPi.addPoint(0, this._grid.maxY-0.5,'OyMax').setVirtual();

        this._xAxis = this._geometryPi.vector('OxMin', 'OxMax');
        this._yAxis = this._geometryPi.vector('OyMin', 'OyMax');

        return this;
    }

    show():Axis{
        this._xAxis.show();
        this._yAxis.show();
        return this;
    }
}