import {Figure} from "./Figure";
import {GeometryPi} from "../GeometryPi";
import {Vector} from "../Vector";
import {Point} from "./Point";
import {Line} from "./Line";
import {Triangle} from "./Triangle";

export class PointCalculated extends Point {
    private _rule: {[key:string] : any};
    private _triangle: Triangle;
    constructor(geom: GeometryPi, name: string) {
        super(geom, name);

        this._triangle = null;

        return this;
    }

    update(): PointCalculated {
        // Calculate the position depending on some rules.
        if(this._rule.rule==='middle'){
            let p1 = this.geometryPi.getPoint(this._rule.p1),
                p2 = this.geometryPi.getPoint(this._rule.p2);

            if(p1===null || p2===null){
                console.log('Middle: one point was not found!');
                return this;
            }

            this.x = (p1.x+p2.x)/2
            this.y = (p1.y+p2.y)/2
        }else if(this._rule.rule==='colinear') {
            let p1 = this.geometryPi.getPoint(this._rule.p1),
                p2 = this.geometryPi.getPoint(this._rule.p2),
                through = this.geometryPi.getPoint(this._rule.through),
                k = 1.0;
            if (p1 === null || p2 === null) {
                console.log('Colinear: one point was not found!');
                return this;
            }

            if (isNaN(Number(this._rule.k))) {
                // TODO: Must determine distance between the data given.
            } else {
                k = +this._rule.k
            }

            this.x = through.x + k * (p2.x - p1.x);
            this.y = through.y + k * (p2.y - p1.y);
        }else if(this._rule.rule === 'perpendicular'){
            let p1 = this.geometryPi.getPoint(this._rule.p1),
                p2 = this.geometryPi.getPoint(this._rule.p2),
                through = this.geometryPi.getPoint(this._rule.through),
                k = 1.0;
            if (p1 === null || p2 === null) {
                console.log('Perpendicular: one point was not found!');
                return this;
            }

            if (isNaN(Number(this._rule.k))) {
                // TODO: Must determine distance between the data given.
            } else {
                k = +this._rule.k
            }

            this.x = through.x + k * (p2.y - p1.y);
            this.y = through.y - k * (p2.x - p1.x);
        }else if(this._rule.rule==='polar'){
            let origin = this.geometryPi.getPoint(this._rule.origin),
                angle = this._rule.angle,
                distance = this._rule.distance;

            // TODO: Make angle and distance computable depending on the data given.
            let degree = 0;
            if(!isNaN(angle)){
                degree = +angle;
            }else if(typeof angle==="function"){
                degree = angle(this.geometryPi, this, this._rule.options)
            }else if(angle.constructor.name==='Arc'){
                degree = angle.angle;
            }

            this.x = origin.x + Math.cos(+degree*Math.PI/180)*(+distance);
            this.y = origin.y - Math.sin(+degree*Math.PI/180)*(+distance);
        }else if(this._rule.rule==='projection'){
            // TODO: Must handle custom axis defined by two points p1, p2
            let p = this.geometryPi.getPoint(this._rule.p);
            if(p===null){
                console.log('Projection: reference point not found');
                return this;
            }

            if(this._rule.axis==='Ox'){
                let O = this.geometryPi.mainGrid.getOrigin();
                this.x = p.x;
                this.y = O.y
            }

            if(this._rule.axis==='Oy'){
                let O = this.geometryPi.mainGrid.getOrigin();
                this.x = O.x;
                this.y = p.y
            }
        }else if(this._rule.rule==='rectangle'){
            let k = +this._rule.k,
                p1 = this.geometryPi.getPoint(this._rule.p1),
                p2 = this.geometryPi.getPoint(this._rule.p2);

            if(p1===null || p2===null){
                console.log('Triangle rectangle: one point was not found!');
                return this;
            }

            let ab = new Vector().from(p1, p2),
                b = ab.norme()/Math.sqrt(1+k*k),
                alpha = Math.atan(k),
                h  = Math.sin(alpha)*b,
                ah = Math.cos(alpha)*b;

            let oa = new Vector(p1.x, p1.y),
                abp = ab.clone().perpendicular().unit().multiply(h),
                abu = ab.clone().unit().multiply(ah);
            oa.add(abp).add(abu);

            this.x = oa.x;
            this.y = oa.y;
        }else if(this._rule.rule==='triangle'){
            if(this._triangle===null){
                console.log('Triangle not found...');
            }

            let P;
            if(this._rule.abc==='A'){
                P = this._triangle.coordinateA();
            }else if(this._rule.abc==='B'){
                P = this._triangle.coordinateB();
            }else if(this._rule.abc==='C'){
                P = this._triangle.coordinateC();
            }

            this.x = P.x;
            this.y = P.y;
        }else if(this._rule.rule==='function'){
            let {x, y} = this._rule.fn(this, this._rule.options);
            this.x = x;
            this.y = y;
        }

        // Move the shape to the current Point.
        this.shape.center(this.x, this.y);

        super.update();
        return this;
    }

    fromFunction(fn: Function, options: any):PointCalculated {
        this._rule = {
            rule: 'function',
            fn,
            options
        };

        this.update();
        return this;
    }

    middle(p1: Point, p2: Point):PointCalculated {
        this._rule = {
            rule: 'middle',
            p1,
            p2
        };

        this.update();
        return this;
    }

    colinear(p1: Point, p2: Point, through: Point, k?:any):PointCalculated{
        if(k===undefined){k = 1.0;}

        this._rule = {
            rule: 'colinear',
            p1,
            p2,
            through,
            k
        }

        this.update();
        return this;
    }

    perpendicular(p1: Point, p2: Point, through: Point, k?:any):PointCalculated{
        if(k===undefined){k = 1.0;}

        this._rule = {
            rule: 'perpendicular',
            p1, p2, through, k
        }
        this.update();
        return this;
    }
    polar(origin: Point, angle: any, distance: any):PointCalculated{
        this._rule = {
            rule: 'polar',
            origin,
            angle: angle,
            distance: distance
        }

        this.update();
        return this;
    }

    projection(p: Point, p1: Point, p2: Point):PointCalculated{
        this._rule = {
            rule: 'projection',
            p,
            p1, p2
        };
        this.update();
        return this;
    }

    fromTriangle(tri: Triangle, abc?: string):PointCalculated{
        this._triangle = tri;

        let triString = tri.name;
        if(abc===undefined){
            abc = 'C'
        }else {
            if (['A', 'B', 'C'].indexOf(abc.toUpperCase()) === -1) {
                abc = 'C';
            } else {
                abc = abc.toUpperCase();
            }
        }

        this._rule = {
            rule: 'triangle',
            triString,
            abc: abc
        }

        this.update();
        return this;
    }

    projectionAxis(p: Point, axis:string):PointCalculated {
        this._rule = {
            rule: 'projection',
            p,
            axis
        };
        this.update();
        return this;
    }
    projectionX(p: Point):PointCalculated{
        return this.projectionAxis(p, 'Ox');
    }

    projectionY(p: Point):PointCalculated{
        return this.projectionAxis(p, 'Oy');
    }

    rectangle(p1: Point, p2: Point, k?:number){
        this._rule = {
            rule: 'rectangle',
            p1, p2,
            k: (k===undefined?4/3:k).toString()
        }
        this.update();
        return this;
    }

}