import {Figure} from "./Figure";
import {GeometryPi, IPoint} from "../GeometryPi";
import {Marker} from "@svgdotjs/svg.js";
import {Point} from "./Point";

export class Line extends Figure {
    private _p1: Point;
    private _p2: Point;
    private _conditionRule: { rule: string, through: Point, k: string };
    private _stopP1: Boolean;
    private _stopP2: Boolean;
    private _arrowStart: Boolean;
    private _arrowEnd: Boolean;
    private _slope: number;

    constructor(geom: GeometryPi, name: string, p1:Point, p2:Point, conditionRule?:{rule: string, through: Point, k?: string}) {
        super(geom, name);

        // Set the points
        this._p1 = p1;
        this._p2 = p2;

        // Set the condition rule for creating the line
        this._conditionRule = {rule:null,through:null,k:null};
        if(conditionRule!==undefined) {
            this._conditionRule.rule = ('rule' in conditionRule) ? conditionRule.rule : '';
            this._conditionRule.through = ('through' in conditionRule) ? conditionRule.through : null;
            this._conditionRule.k = ('k' in conditionRule) ? conditionRule.k : '';
        }

        // Create the shape
        this.shape = geom.draw.line(
            0, 0, 0, 0
        ).stroke('black');

        // Update the line using the condition rules.
        this.update();

        return this;
    }

    private _getCoordinates(){
        let referencePoint = this.geometryPi.getPoint(this._p1),
            p2 = this.geometryPi.getPoint(this._p2),
            l1:IPoint, l2:IPoint;

        this._calculateSlope();

        // Is there a condition rule ?
        if(this._conditionRule.rule==='p'){
            this._calculateSlopePerpendicular();
            if(this._conditionRule.through!==null){
                referencePoint = this._conditionRule.through;
            }
        }

        // TODO: really need to stop the line ?
        if(this._conditionRule.k==='') {
            this.stopP2 = false;
        }else{
            // Determine the length given by k.
            // Might be a number or a reference.
            // TODO: Handle K parameter inside condition rules!
        }


        if(this._stopP1){
            l1 = referencePoint;
        }else{
            if(this.slope!==Infinity) {
                l1 = this._getLinePosAtX((referencePoint.x < p2.x) ? 0 : this.geometryPi.width, referencePoint);
            }else{
                l1 = {x: referencePoint.x, y: 0}
            }
        }

        if(this._stopP2){
            l2 = p2;
        }else{
            if(this.slope!==Infinity) {
                l2 = this._getLinePosAtX((referencePoint.x < p2.x) ? this.geometryPi.width : 0, referencePoint);
            }else{
                l2 = {x: referencePoint.x, y: this.geometryPi.height}
            }
        }

        return {x1: l1.x, y1: l1.y, x2: l2.x, y2: l2.y};
    }

    private _getLinePosAtX(x:number, p1:IPoint):IPoint{
        if(this.slope!==Infinity) {
            return {
                x,
                y: x*this.slope+(p1.y - p1.x * this.slope)
            };
        }else{
            return {
                x,
                y: 0
            };
        }
    }

    private _calculateSlope():number {
        let dx: number, dy: number,
            p1 = this.geometryPi.getPoint(this._p1),
            p2 = this.geometryPi.getPoint(this._p2);

        dx = p2.x-p1.x;
        dy = p2.y-p1.y;

        // Same points
        if(dx===0 && dy===0){
            this._slope = null;
        }

        // Vertical
        if(dx===0){
            this._slope = Infinity;
        }

        // Horizontal
        if(dy===0){
            this._slope = 0;
        }

        // Oblique
        this._slope = dy/dx;

        return this._slope;
    }

    private _calculateSlopePerpendicular():number {
        if(this._slope=== 0){
            this._slope = Infinity;
        }else if(this._slope===Infinity){
            this._slope = 0;
        }else{
            this._slope = -1/this._slope;
        }
        return this._slope;
    }

    update(): Line {
        // Move the shape to the current Point.
        let {x1, y1, x2, y2} = this._getCoordinates();
        this.shape.plot(x1, y1, x2, y2);

        // Add the markers if necessary.
        if(this._arrowEnd) {
            this.shape.marker('end', this.markerEnd);
        }
        if(this._arrowStart) {
            this.shape.marker('start', this.markerStart);
        }
        super.update();
        return this;
    }

    segment(): Line {
        this._stopP1 = true;
        this._stopP2 = true;
        this._arrowEnd = false;
        this._arrowStart = false;
        this.update();
        return this;
    }

    vector(): Line {
        this._stopP1 = true;
        this._stopP2 = true;
        this._arrowEnd = true;
        this._arrowStart = false;
        this.update();
        return this;
    }

    halfRule(): Line {
        this._stopP1 = true;
        this._stopP2 = false;
        this._arrowEnd = false;
        this._arrowStart = false;
        this.update();
        return this;
    }

    line(): Line {
        this._stopP1 = false;
        this._stopP2 = false;
        this._arrowEnd = false;
        this._arrowStart = false;
        this.update();
        return this;
    }

    perpendicular(through?:Point): Line {
        // Remove the condition
        if(this._conditionRule.rule==='p'){
            this._conditionRule.rule = '';
        }

        this.line();
        this._conditionRule.rule = 'p';
        this._conditionRule.through = through===undefined?null:through;
        this._conditionRule.k = '';

        this.update();
        return this;
    }

    /* Label functions */
    moveLabel(): Line {
        if(this.label===null){return this;}

        let x =(this._p1.x+this._p2.x)/2,
            y = (this._p1.y+this._p2.y)/2;

        if(x<0){x = 0;}
        if(x>super.geometryPi.width){x = +super.geometryPi.width}

        if(y<0){y = 0;}
        if(y>super.geometryPi.height){y = +super.geometryPi.height}

        this.label.move(x, y)
        return this;
    }

    labelDistance(round?:number, scale?:number): Line {
        this.label.showDistance(
            this._p1, this._p2,
            round===undefined?0:round,
            scale===undefined?1:scale
        );
        return this;
    }

    /* Comparison */
    isColinar(L: Line): boolean {
        // Must have the same slope
        return L.slope===this.slope;
    }
    isSame(L: Line): boolean {
        // Muse have the same slope, same size and same direction
        return this.isColinar(L) && L.size===this.size && L.direction===this.direction;
    }
    isOpposed(L: Line): boolean {
        // Muse have the same slope, same size, but opposite direction
        return this.isColinar(L) && L.size===this.size && L.direction!==this.direction;
    }
    isPerpendicular(L: Line): Boolean {
        return this.slope===L.slopePerendicular;
    }
    /* Getter and setter*/
    get p1(): Point {
        return this._p1;
    }

    set p1(value: Point) {
        this._p1 = value;
    }

    get p2(): Point {
        return this._p2;
    }

    set p2(value: Point) {
        this._p2 = value;
    }

    get stopP1(): Boolean {
        return this._stopP1;
    }

    set stopP1(value: Boolean) {
        this._stopP1 = value;
    }

    get stopP2(): Boolean {
        return this._stopP2;
    }

    set stopP2(value: Boolean) {
        this._stopP2 = value;
    }

    get arrowStart(): Boolean {
        return this._arrowStart;
    }

    set arrowStart(value: Boolean) {
        this._arrowStart = value;
    }

    get arrowEnd(): Boolean {
        return this._arrowEnd;
    }

    set arrowEnd(value: Boolean) {
        this._arrowEnd = value;
    }

    get slopePerendicular(): number {
        if(this.slope===0){
            return Infinity
        }else{
            return Math.round(-1/this.slope*100000)/100000;
        }
    }
    get slope():number {
        return Math.round(this._slope*100000)/100000;
    }

    get size(): number{
        return Math.round(
            Math.sqrt(
                Math.pow(this._p2.x-this._p1.x, 2) +
                Math.pow(this._p2.y-this._p1.y, 2)
            )*10
        )/10;
    }

    get direction(): number{
        const dx = this.p2.x-this.p1.x;

        if(dx>0){
            return 1;
        }else if(dx <0){
            return -1;
        }else{
            return (this.p2.y-this.p1.y)>0?1:-1;
        }
    }
}