import {Figure} from "./Figure";
import {GeometryPi, IPoint} from "../GeometryPi";
import {Marker, Shape} from "@svgdotjs/svg.js";
import {Arc} from "./Arc";
import {Point} from "./Point";

export class Polygon extends Figure {
    private _points: Point[];
    private _rectangleMark: Boolean;
    private _angleMark: Boolean;
    private _angleArcs: {[key: string]:Arc}

    constructor(geom: GeometryPi, name: string) {
        super(geom, name);

        this._rectangleMark = false;
        this._angleMark = false;
        this.shape = geom.draw.polygon([0, 0, 0, 0]).stroke('black').fill('none');

        this._angleArcs = {};
        return this;
    }

    setPoints(...points: Point[]): Polygon {
        this._points = [];
        this.addPoints(...points);
        return this;
    }

    addPoints(...points: Point[]): Polygon {
        for (let p of points) {
            this._points.push(p);
        }
        this.update();
        return this;
    }

    private _getPath(): number[] {
        let arr: number[] = [];

        for (let p of this._points) {
            let point = this.geometryPi.getPoint(p);
            if (point === null) {
                continue;
            }
            arr.push(point.x);
            arr.push(point.y);
        }

        return arr;
    }

    update(): Polygon {
        // Move the shape to the current Point.
        this.shape.plot(this._getPath());

        // Update the rectangle marks
        this.updateAngleMark();
        super.update();
        return this;
    }

    angleMark(value:Boolean):Polygon{
        this._angleMark = value!==false;
        this.updateAngleMark()
        return this;
    }

    updateAngleMark():Polygon{
        if(this._angleMark===false && this._rectangleMark===false){
            for(let a in this._angleArcs){
                this._angleArcs[a].hide();
            }
            return this;
        }

        let pts = [];
        pts.push(this.geometryPi.getPoint(this._points[this._points.length-1]));
        for(let p of this._points){
            pts.push(this.geometryPi.getPoint(p));
        }
        pts.push(this.geometryPi.getPoint(this._points[0]));

        for(let i=1; i<pts.length-1; i++){
            let previous = pts[(i-1)],
                point = pts[i],
                next = pts[i+1],
                name = `${previous.name}${point.name}${next.name}`;

            if(name in this._angleArcs){
                // Update this shape.
                this._angleArcs[name].square(this._rectangleMark).update();
            }else{
                this._angleArcs[name] = new Arc(this.geometryPi, name).center(point).radius(40).angleABC([next, point, previous]).square(this._rectangleMark);
            }

            // We don't want to display the angle marks
            if(this._angleMark===false){
                this._angleArcs[name].isSquare?this._angleArcs[name].show():this._angleArcs[name].hide();
            }else{
                this._angleArcs[name].show();
            }

        }
        return this;
    }

    rectangleMark(value:Boolean):Polygon {
        this._rectangleMark = value!==false;
        this.updateAngleMark()
        return this;
    }

    logAngles(){
        for(let m in this._angleArcs){
            console.log(m, this._angleArcs[m].angle);
        }
    }


    get points(): Point[] {
        return this._points;
    }

}