import {Figure} from "./Figure";
import {GeometryPi} from "../GeometryPi";
import {Circle, Path} from "@svgdotjs/svg.js";
import {Grid} from "../Grid";
import {PointCalculated} from "./PointCalculated";
import {Point} from "./Point";

export class PointUser extends Point {
    private _draggable: Boolean;
    private _dragShape: Circle;
    private _dragGrid: Grid;
    private _dragCenter: PointUser;
    private _dragDistance: number;
    private _dragTick: number;
    private _afterDrag: Function;

    constructor(geom: GeometryPi, name: string, x: number, y: number) {
        super(geom, name);
        this._dragGrid = null;
        this._dragDistance = null;
        this._dragTick = null;
        this._afterDrag = null;

        this.x = x;
        this.y = y;

        this.update();
        return this;
    }

    makeDraggable(options?: { [key: string]: any }): Point {
        // It's already draggable.
        if (this._draggable) {
            super.makeDraggable();
            return this;
        }

        // Mark the point as draggable
        this._draggable = true;

        // Create the drag shape
        this._dragShape = this.geometryPi.draw
            .circle(50)
            .center(this.x, this.y)
            .fill('rgba(8,255,103,0.1)')
            .stroke('rgba(79,172,113,0.84)');

        // Make it draggable
        this._dragShape.draggable();

        // Create the on move function
        let geom = this.geometryPi,
            point = this;

        function dragMove(e: any) {
            const {handler, box} = e.detail;
            let {x, y} = box;

            e.preventDefault();

            // Prevent going out of the draw limits.
            if (x < 0) {
                return;
            }
            if (x > geom.width - box.width / 2) {
                return;
            }
            if (y < 0) {
                return;
            }
            if (y > geom.height - box.width / 2) {
                return;
            }

            // Default behavior
            handler.move(x, y);

            // Handle the grid if asked
            if (point.dragGrid !== null) {
                let pt = point.dragGrid.nearestPoint({x: x + box.width / 2, y: y + box.height / 2});
                handler.move(pt.x - box.width / 2, pt.y - box.height / 2);
            }

            // The constrain is a distance to a point (on a circle).
            if (point.dragCenter !== null && point.dragDistance !== null && point.dragDistance > 0) {
                // If there is a tick defined:
                let angle = Math.atan(-(box.y - point.dragCenter.y) / (box.x - point.dragCenter.x)) * 180 / Math.PI;
                if(point.dragCenter.x>box.x){angle = 180+angle;}

                // Change the tick value.
                if(point._dragTick!==null && point._dragTick>=0){
                    angle = Math.round(angle / point._dragTick) * point._dragTick;
                }

                handler.move(
                    point.dragCenter.x + Math.cos(angle*Math.PI/180) * point.dragDistance -box.width/2,
                    point.dragCenter.y - Math.sin(angle*Math.PI/180) * point.dragDistance -box.height/2
                )
            }

            if (options !== undefined && options.fn !== undefined) {
                options.fn(geom, point, box, handler, options.args);
            }

            // Move the point shape.
            point.x = handler.el.cx();
            point.y = handler.el.cy();

            // Update the figures and labels
            for (let f of geom.figures) {
                f.update();
            }

            // After drag
            if(point._afterDrag!==null){
                point._afterDrag(geom, point);
            }
        }

        this._dragShape.on('dragmove', dragMove);

        this._dragShape.front();
        super.makeDraggable();
        return this;
    }

    afterDrag(afterDragFunction: Function):PointUser{
        this._afterDrag = afterDragFunction;
        return this;
    }

    setDragGrid(grid: Grid): PointUser {
        this._dragGrid = grid;
        return this;
    }

    setDistanceTo(center: PointUser, distance?: number, tick?: number): PointUser {
        this._dragCenter = center;
        this._dragDistance = (distance === undefined || distance === null ) ? this.distance(this, center) : distance;
        this._dragTick = tick === undefined ? null : tick;
        return this;
    }

    /* Getter and setter*/
    get dragShape(): Circle {
        return this._dragShape;
    }

    set dragShape(value: Circle) {
        this._dragShape = value;
    }

    get dragGrid(): Grid {
        return this._dragGrid;
    }

    get dragCenter(): PointUser {
        return this._dragCenter;
    }

    get dragDistance(): number {
        return this._dragDistance;
    }
}