import {Figure} from "./Figure";
import {GeometryPi, IPoint} from "../GeometryPi";

export class Point extends Figure{
    private _x: number;
    private _y: number;
    private _scale: number;

    constructor(geom:GeometryPi, name:string) {
        super(geom, name);

        this.isPoint = true;
        this._scale = 6;
        this.shape = geom.draw.path(
            `M${- this._scale},${- this._scale} L${+ this._scale},${+ this._scale} M${+ this._scale},${- this._scale} L${- this._scale},${+ this._scale}`
        ).stroke('black').center(0, 0);
        return this;
    }

    update(): Point {
        // Move the shape to the current Point.
        this.shape.center(this._x, this._y);
        super.update();
        return this;
    }

    moveLabel(): Point {
        if(this.label===null){return this;}
        this.label.move(this._x, this._y)

        return this;
    }

    labelCoordinate(round?:number, scale?:number): Point {
        this.label.showCoordinates(
            this,
            round===undefined?0:round,
            scale===undefined?1:scale
        );
        return this;
    }

    distanceTo(p: Point):number{
        return this.distance(this, p);
    }

    /* Getter and setter*/
    get x(): number {
        return this._x;
    }

    set x(value: number) {
        this._x = value;
    }

    get y(): number {
        return this._y;
    }

    set y(value: number) {
        this._y = value;
    }

    get coord():IPoint {
        return this.geometryPi.mainGrid.getPointCoordinate(this._x, this._y);
    }

    get xCoord():number {
        return this.coord.x;
    }
    get yCoord():number {
        return this.coord.y
    }

    get scale(): number {
        return this._scale;
    }

    set scale(value: number) {
        this._scale = value;
    }
}