import {Figure} from "./Figure";
import {GeometryPi, IPoint} from "../GeometryPi";
import {Marker, Shape} from "@svgdotjs/svg.js";
import {Arc} from "./Arc";
import {Point} from "./Point";

export class Path extends Figure {
    private _d: string;

    constructor(geom: GeometryPi, name: string) {
        super(geom, name);

        this.shape = geom.draw.path('').stroke('black').fill('none');
        return this;
    }

    setPath(d: string): Path {
        this._d = ''+d;
        this.update()
        return this;
    }

    update(): Path {
        // Move the shape to the current Point.
        this.shape.plot(this._d);

        // Update the rectangle marks
        super.update();
        return this;
    }

}