import {Figure} from "./Figure";
import {GeometryPi, IPoint} from "../GeometryPi";
import {Point} from "./Point";
import {Vector} from "../Vector";

export class Arc extends Figure {
    private _center: Point;
    private _radius: Point[]|number;
    private _angleCoordinates: Point[];

    private _startPoint: IPoint;
    private _endPoint: IPoint;
    private _startAngle: number;
    private _endAngle: number;
    private _angle: number;
    private _theRadius: number;
    private _markAngle: Boolean;
    private _square: Boolean;
    private _sector: Boolean;

    private _labelDistanceToCenter: number;

    constructor(geom: GeometryPi, name?: string) {
        super(geom, name);
        this._center = null;
        this._radius = 40;
        this._angleCoordinates = null;

        this._markAngle = false;
        this._square = false;
        this._sector = false;

        this.label.center().middle();
        this._labelDistanceToCenter = 1.75;


        // Create the shape
        this.shape = geom.draw.path(
            this.getPath()
        ).stroke('black').fill('None');

        return this;
    }

    center(center: Point):Arc {
        this._center = this.geometryPi.getPoint(center);
        this.update();
        return this;
    }

    radius(radius:Point[]|number):Arc {
        this._radius = radius;
        this.update();
        return this;
    }

    angleABC(angles:Point[]):Arc{
        this._angleCoordinates = angles;
        this._startAngle = null;
        this._endAngle = null;
        this.update();
        return this;
    }

    angleStartStop(start: number, stop: number):Arc{
        this._angleCoordinates = null;
        this._startAngle = +start;
        this._endAngle = +stop;
        this.update();
        return this;
    }

    markAngle():Arc{
        this._markAngle=!this._markAngle;
        this.update();
        return this;
    }

    marker(pos:string='end', enable?:Boolean):Arc{
        if(pos==='end') {
            if (enable === false) {
                this.shape.marker('end', null);
            } else {
                this.shape.marker('end', this.markerEnd);
            }
        }else{
            if (enable === false) {
                this.shape.marker('start', null);
            } else {
                this.shape.marker('start', this.markerStart);
            }
        }
        return this;
    }


    square(enable?:Boolean):Arc{
        this._square = enable!==false;
        this.update();
        return this;
    }

    sector(enable?:Boolean):Arc{
        this._sector = enable!==false;
        this.update();
        return this;
    }

    moveLabel(): Arc {
        if(this.label===null){return this;}

        // Direction of the target point.
        let dir = {
            x: (this._angle<180?1:-1)*(this._startPoint.x + this._endPoint.x - 2*this._center.x),
            y: -(this._angle<180?1:-1)*(this._startPoint.y + this._endPoint.y - 2*this._center.y)
        }

        this.label.move(
            this._center.x + dir.x*(this._theRadius + this.label.labelDistance )/Math.sqrt(dir.x*dir.x+dir.y*dir.y),
            this._center.y - dir.y*(this._theRadius + this.label.labelDistance )/Math.sqrt(dir.x*dir.x+dir.y*dir.y)
        );
        return this;
    }

    /**
     * Get coordinate by radius / angle
     * Reference: https://stackoverflow.com/questions/5736398/how-to-calculate-the-svg-path-for-an-arc-of-a-circle
     * @param centerX
     * @param centerY
     * @param radius
     * @param angleInDegrees
     */
    polarToCartesian(centerX:number, centerY:number, radius:number, angleInDegrees:number):IPoint {
        var angleInRadians = -(angleInDegrees) * Math.PI / 180.0;

        return {
            x: centerX + (radius * Math.cos(angleInRadians)),
            y: centerY + (radius * Math.sin(angleInRadians))
        };
    }

    cartesianToAngle(origin:Point, handle:Point):number {
        let angle,
            dx = handle.x-origin.x,
            dy = -(handle.y-origin.y);

        angle = (handle.x-origin.x===0)?90:Math.atan(dy/dx)*180.0/Math.PI;

        // Depending on the position in the grid, modify the value.
        if(dx>=0){
            if(dy>=0){
                // 0 -> 90

            }else{
                // 270->360
                while(angle<270){
                    angle+=180
                }
            }
        }else{
            if(dy>=0){
                // 90->180
                while(angle<90){
                    angle+=180
                }
            }else{
                // 180->270
                while(angle<180){
                    angle+=180
                }
            }
        }


        return angle;
    }
    getAngles():{startAngle: number, endAngle: number}{
        let startAngle=0,
            endAngle=0;

        if(this._angleCoordinates===null){
            return {
                startAngle:+this._startAngle,
                endAngle: +this._endAngle
            }
        }

        if(this._angleCoordinates.length===2){
            startAngle = +this._angleCoordinates[0];
            endAngle = +this._angleCoordinates[1];
        }else if(this._angleCoordinates.length===3){
            // Get the angles
            startAngle = this.cartesianToAngle(this._angleCoordinates[1], this._angleCoordinates[0]);
            endAngle = this.cartesianToAngle(this._angleCoordinates[1], this._angleCoordinates[2]);
        }

        return {startAngle, endAngle}
    }

    getPath():string{
        if(this.center===null){return '';}
        if(this.radius===null){return '';}

        if(this._angleCoordinates===null){
            if(this._startAngle===undefined || this._endAngle===undefined){return ''}
        }
        let {startAngle, endAngle} = this.getAngles(),
            r = (this.isSquare && this._square)?this.getRadius/2:this.getRadius,
            start = this.polarToCartesian(this._center.x, this._center.y, r, startAngle),
            end = this.polarToCartesian(this._center.x, this._center.y, r, endAngle);

        // Store the data
        this._angle = endAngle-startAngle;
        this._startAngle = +startAngle;
        this._endAngle = +endAngle;
        this._startPoint = start;
        this._endPoint = end;
        this._theRadius = r;

        if(this._square && this.isSquare){
            return this._describeSquare();
        }else{
            return this._describeArc();
        }
    }

    private _describeSquare():string{
        if(this._center===null){return '';}
        if(this._startAngle===null || this._endAngle===null){return '';}

        return [
            "M", this._startPoint.x, this._startPoint.y,
            "l", (this._endPoint.x-this._center.x), (this._endPoint.y-this._center.y),
            "L", this._endPoint.x, this._endPoint.y
        ].join(" ");
    }

    private _describeArc():string{
        if(this._center===null){return '';}
        if(this._theRadius===null || this._theRadius===0){return '';}
        if(this._startAngle===null || this._endAngle===null){return '';}

        let largeArcFlag = (this._angle+360)%360 <= 180 ? 0: 1,
            swipeFlag = 0;

        if(this._markAngle) {
            if (this._startAngle > this._endAngle) {
                largeArcFlag = (largeArcFlag + 1) % 2;
                swipeFlag = 1;
            }
        }

        let p = [
            "M", this._startPoint.x, this._startPoint.y,
            "A", this._theRadius, this._theRadius, 0, largeArcFlag, swipeFlag, this._endPoint.x, this._endPoint.y
        ];

        if(this._sector){
            p = p.concat(['L', this._center.x, this._center.y, 'L', this._startPoint.x, this._startPoint.y]);
        }

        return p.join(" ");
    }

    update(): Arc {
        if(this._center===null){return this;}
        if(this._angleCoordinates===null){
            if(this._startAngle===undefined || this._endAngle===undefined) {
                return this;
            }
        }

        // Move the shape to the current Point.
        this.shape.plot(this.getPath());
        super.update();
        return this;
    }

    /* Getter and setter*/
    get getRadius():number {
        if(Array.isArray(this._radius)){
            return this.distance(this._radius[0], this._radius[1]);
        }else{
            return this._radius;
        }
    }

    get angle():number{
        return this._angle;
    }

    get isSquare():Boolean{
        if(this._angle===undefined){return false;}

        return this._angle.toFixed(4) === '90.0000';
    }
}